import { api } from 'src/boot/apis';
import fnStore from '../fn.store';
import { checkIfObject } from 'src/assets/scripts/functions';

async function loginWithPassword(payload) {
  return await api
    .post('/login', payload)
    .then((res) => {
      if (process.env.DEBUGGING) console.log(res.data);
      let data = res.data;
      if (data?.success) {
        let token = data?.token;
        fnStore.addAuth(token);
      }
      return data;
    })
    .catch((e) => {
      if (process.env.DEBUGGING) console.log(e);
      return e;
    });
}

function searchUserInfo(info, allResult = false) {
  let userInfo = Object.values(this.organizationUsers).filter((usr) => {
    let res = false;
    Object.keys(usr).forEach((e) => {
      if (usr[e] == info) res = true;
    });
    return res;
  });
  return allResult ? userInfo : userInfo[0] || {};
}

function updateAccount(userInfo = {}) {
  if (checkIfObject(userInfo)) {
    Object.keys(userInfo).forEach((k) => {
      this.user[k] = userInfo[k];
    });
  }
}

function updateOrganization(orgId, orgInfo = {}) {
  if (orgId === null || orgId === undefined) return;
  if (checkIfObject(orgInfo)) {
    Object.keys(orgInfo).forEach((k) => {
      this.organizations[orgId][k] = orgInfo[k];
    });
  }
}

// Organization User
function addOrgUser(userId, userInfo = {}, orgId = null) {
  if (userId === null || userId === undefined) return;
  if (orgId === null) orgId = this.activeOrgID;
  this.organizationUsers[orgId][userId] = userInfo;
}

function updateOrgUser(userId, userInfo = {}, orgId = null) {
  if (userId === null || userId === undefined) return;
  if (orgId === null) orgId = this.activeOrgID;
  this.organizationUsers[orgId][userId] = userInfo;
}

function deleteOrgUser(userId, orgId = null) {
  if (userId === null || userId === undefined) return;
  if (orgId === null) orgId = this.activeOrgID;
  delete this.organizationUsers[orgId][userId];
}

function logout() {
  fnStore.removeAuth();
}

