// Subscription
function subscriptionRemaining(date_now = new Date(), unit = 'days') {
  const { date } = require('quasar');
  return date.getDateDiff(
    this.selectedOrg?.subscriptionPlan?.subscription_end,
    date_now,
    unit
  );
}

function subscriptionIsExpired(date_now = new Date()) {
  return Number(this.subscriptionRemaining(date_now, 'seconds')) < 1;
}

export {
  updateAccount,
  loginWithPassword,
  searchUserInfo,
  addOrgUser,
  updateOrganization,
  updateOrgUser,
  deleteOrgUser,
  logout,
  addOrgFiles,
  updateOrgFiles,
  deleteOrgFiles,
  addOrgBrand,
  updateOrgBrand,
  deleteOrgBrand,
  subscriptionRemaining,
  subscriptionIsExpired,
};
