___CSS_LOADER_EXPORT___.push([module.id, ".q-color-picker {\n  border-radius: 8px;\n}\n.q-color-picker .q-panel-parent {\n  border-radius: 8px;\n}\n.q-color-picker__spectrum {\n  border-radius: 8px;\n}\n.q-color-picker__spectrum-white, .q-color-picker__spectrum-black {\n  border-radius: 8px;\n}\n.q-color-picker__palette-tab, .q-color-picker__palette-rows {\n  border-radius: 8px;\n}\n.q-color-picker .q-slider__focus-ring {\n  text-align: center;\n  opacity: 1 !important;\n  background: white !important;\n}\n.q-color-picker .q-slider__focus-ring:hover, .q-color-picker .q-slider__focus-ring:active, .q-color-picker .q-slider__focus-ring:focus-within, .q-color-picker .q-slider__focus-ring:focus-visible {\n  transform: scale3d(1, 1, 1) !important;\n}\n.q-color-picker .q-slider__focus-ring:before {\n  opacity: 1 !important;\n  border-radius: 50%;\n  background: white !important;\n  color: var(--ringColor) !important;\n}\n.q-color-picker .q-slider__track {\n  height: 20px !important;\n}\n.q-color-picker .q-slider__track:before {\n  background: var(--barColor) !important;\n}\n.q-color-picker .q-slider__track-container:hover .q-slider__focus-ring, .q-color-picker .q-slider__track-container:active .q-slider__focus-ring, .q-color-picker .q-slider__track-container:focus-within .q-slider__focus-ring, .q-color-picker .q-slider__track-container:focus-visible .q-slider__focus-ring {\n  transform: scale3d(1, 1, 1) !important;\n}\n.q-color-picker .q-slider__thumb {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n}\n.q-color-picker .q-slider__thumb svg {\n  display: none;\n}", "",{"version":3,"sources":["webpack://./src/components/Helpers/ColorPicker.vue","webpack://./src/css/quasar.variables.scss"],"names":[],"mappings":"AAEA;EACE,kBCgFO;ADjFT;AAKE;EACE,kBC2EK;AD9ET;AAKE;EACE,kBCwEK;AD3ET;AAII;EAEE,kBCqEG;ADxET;AAOI;EAEE,kBC+DG;ADrET;AAUI;EAOE,kBAAA;EACA,qBAAA;EACA,4BAAA;AAdN;AAMM;EAIE,sCAAA;AAPR;AAYM;EACE,qBAAA;EACA,kBAAA;EACA,4BAAA;EACA,kCAAA;AAVR;AAaI;EACE,uBAAA;AAXN;AAYM;EACE,sCAAA;AAVR;AAiBU;EACE,sCAAA;AAfZ;AAoBI;EACE,oBAAA;EACA,mBAAA;EACA,uBAAA;AAlBN;AAmBM;EACE,aAAA;AAjBR","sourcesContent":["@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';\n\n.q-color-picker {\n  border-radius: $border;\n  // &__sliders {\n  //   padding: 0;\n  // }\n  & .q-panel-parent {\n    border-radius: $border;\n  }\n  &__spectrum {\n    border-radius: $border;\n    &-white,\n    &-black {\n      border-radius: $border;\n    }\n  }\n  &__palette {\n    &-tab,\n    &-rows {\n      border-radius: $border;\n    }\n  }\n  & .q-slider {\n    &__focus-ring {\n      &:hover,\n      &:active,\n      &:focus-within,\n      &:focus-visible {\n        transform: scale3d(1, 1, 1) !important;\n      }\n      text-align: center;\n      opacity: 1 !important;\n      background: white !important;\n      &:before {\n        opacity: 1 !important;\n        border-radius: 50%;\n        background: white !important;\n        color: var(--ringColor) !important;\n      }\n    }\n    &__track {\n      height: 20px !important;\n      &:before {\n        background: var(--barColor) !important;\n      }\n      &-container {\n        &:hover,\n        &:active,\n        &:focus-within,\n        &:focus-visible {\n          & .q-slider__focus-ring {\n            transform: scale3d(1, 1, 1) !important;\n          }\n        }\n      }\n    }\n    &__thumb {\n      display: inline-flex;\n      align-items: center;\n      justify-content: center;\n      & svg {\n        display: none;\n      }\n    }\n  }\n}\n","// Quasar SCSS (& Sass) Variables\n// --------------------------------------------------\n// To customize the look and feel of this app, you can override\n// the Sass/SCSS variables found in Quasar's source Sass/SCSS files.\n\n// Check documentation for full list of Quasar variables\n\n// Your own variables (that are declared here) and Quasar's own\n// ones will be available out of the box in your .vue/.scss/.sass files\n\n// It's highly recommended to change the default colors\n// to match your app's branding.\n// Tip: Use the \"Theme Builder\" on Quasar's documentation website.\n// https://quasar.dev/style/sass-scss-variables\n\n// Themes\n$primary: #121212;\n$primary2: #5b6279;\n$secondary: #2b67ff;\n$accent: #f6f9ff;\n$accent2: #fcfcfd;\n\n$dark: #0a122a;\n$dark-page: #0a122a;\n\n$gm-logo: (\n  color-1: #3867f6,\n  color-2: #101010,\n  color-3: #d2fd51,\n);\n\n$positive: #1976d2;\n$negative: #d92d20;\n$info: #00a761;\n$warning: #fec84b;\n\n/// Replace `$search` with `$replace` in `$string`\n/// @author Hugo Giraudel\n/// @param {String} $string - Initial string\n/// @param {String} $search - Substring to replace\n/// @param {String} $replace ('') - New value\n/// @return {String} - Updated string\n@function str-replace($string, $search, $replace: '') {\n  $index: str-index($string, $search);\n\n  @if $index {\n    @return str-slice($string, 1, $index - 1) + $replace +\n      str-replace(\n        str-slice($string, $index + str-length($search)),\n        $search,\n        $replace\n      );\n  }\n\n  @return $string;\n}\n\n// Custom Variables\n$placeholder-color: rgba($primary, 0.5);\n$border-color: #e0e0e0;\n\n$translucent: 0.3;\n$transition-durations: (\n  normal: 0.3s,\n  md: 0.5s,\n  lg: 0.7s,\n  xl: 1s,\n);\n\n$generic-hover-transition: map-get($transition-durations, 'normal')\n  cubic-bezier(0.25, 0.8, 0.5, 1);\n\n$letter-spacing: (\n  standard: -0.5px,\n  normal: 0px,\n  sm: 0.1rem,\n  md: 0.2rem,\n  lg: 0.3rem,\n);\n\n// Growmodo Extra Colors\n$gmc-green: #bcf200;\n\n$border: 8px;\n$borders: (\n  normal: 1px,\n  md: 1.5px,\n  lg: 2px,\n  xl: 3px,\n);\n\n$active-bg: #ecfdf3;\n$active-color: #027a48;\n$negative-bg: #fef3f2;\n$negative-color: #b42318;\n$disable-bg: #f8f9fc;\n$disable-color: #363f72;\n$blue-bg: #eff8ff;\n$blue-color: $secondary;\n$warning-bg: #fffaeb;\n$warning-color: #b54708;\n\n$font-extra-light: 100;\n$font-thin: 150;\n$font-light: 200;\n$font-normal: 400;\n$font-medium: 500;\n$font-demi-bold: 600;\n$font-bold: 700;\n$font-extra-bold: 750;\n$font-black: 800;\n$font-extra-black: 900;\n\n// Custom Shadow from Untitled UI\n$shadow-ui: 0px 12px 16px -4px rgba(16, 24, 40, 0.08),\n  0px 4px 6px -2px rgba(16, 24, 40, 0.03);\n$shadow-gm: 4px 4px 0 0 #2b67ff;\n\n// Quasar Defaults Overwrite\n$typography-font-family: 'TT Norms Pro', 'Roboto', '-apple-system',\n  'Helvetica Neue', Helvetica, Arial, sans-serif;\n\n$button-shadow: $shadow-ui;\n$button-shadow-active: $shadow-ui;\n$table-box-shadow: $shadow-ui;\n$layout-shadow: $shadow-ui;\n$menu-box-shadow: $shadow-ui;\n$rating-shadow: $shadow-ui;\n\n// Borders Radius\n$generic-border-radius: $border;\n$button-border-radius: $border;\n$button-push-border-radius: $border;\n\n$toolbar-title-letter-spacing: map-get($letter-spacing, 'standard');\n\n$h1: (\n  size: 2.7rem,\n  line-height: 3.5rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 300,\n);\n$h2: (\n  size: 2.2rem,\n  line-height: 3.3rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 300,\n);\n$h3: (\n  size: 1.8rem,\n  line-height: 2.3rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 400,\n);\n$h4: (\n  size: 1.7rem,\n  line-height: 2rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 400,\n);\n$h5: (\n  size: 1.3rem,\n  line-height: 1.8rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 400,\n);\n$h6: (\n  size: 1.1rem,\n  line-height: 1.5rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 500,\n);\n$subtitle1: (\n  size: 1rem,\n  line-height: 1.5rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 400,\n);\n$subtitle2: (\n  size: 0.8rem,\n  line-height: 1rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 500,\n);\n$body1: (\n  size: 1rem,\n  line-height: 1.4rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 400,\n);\n$body2: (\n  size: 0.95rem,\n  line-height: 1.3rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 400,\n);\n$overline: (\n  size: 0.8rem,\n  line-height: 0.9rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 500,\n);\n$caption: (\n  size: 0.85rem,\n  line-height: 1rem,\n  letter-spacing: map-get($letter-spacing, 'standard'),\n  weight: 400,\n);\n\n$body-font-size: map-get($body2, 'size');\n\n$input-font-size: map-get($body2, 'size');\n$input-label-color: $placeholder-color;\n$input-text-color: rgba($primary, 1);\n\n$tooltip-background: $primary;\n$tooltip-fontsize: 0.9rem;\n\n$item-base-color: $accent;\n"],"sourceRoot":""}]);
