<template>
  <q-page padding class="container">
    <q-toolbar class="q-pb-sm q-px-none">
      <q-toolbar-title>
        <div class="font-medium text-h5">{{ title }}</div>
      </q-toolbar-title>
    </q-toolbar>

    <HelpCenter
      v-if="!external"
      :noclose="true"
      bgcolor="accent2"
      class="full-width"
      style="height: calc(100vh - 125px) !important"
    />

    <div v-else class="text-center full-width q-pt-xl">
      Connecting to help page...
    </div>
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import HelpCenter from 'src/components/HelpCenter.vue';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'HelpCenterPage',

  props: {
    external: {
      type: Boolean,
      default: true,
    },
  },

  components: { HelpCenter },

  setup() {
    return {
      title: 'Help Center',
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    if (this.external) {
      const { externalLinks } = useGrowmodo();
      window.location.href = externalLinks?.helpCenter;
    }
  },
});
</script>
