// Layouts
import EmptyLayout from 'layouts/EmptyLayout.vue';
import MainLayout from 'layouts/MainLayout.vue';

// Pages
import IndexPage from 'src/pages/IndexPage.vue';
import BrandsPage from 'src/pages/BrandsPage.vue';
import FilesPage from 'src/pages/FilesPage.vue';
import HelpCenterPage from 'src/pages/HelpCenterPage.vue';
import DiscoverPage from 'src/pages/DiscoverPage.vue';
import GiftPage from 'src/pages/GiftPage.vue';
import AccountSettingsPage from 'src/pages/AccountSettingsPage.vue';

// Misc
import AuthenticationPage from 'src/pages/Misc/AuthenticationPage.vue';
import SignupPage from 'app/src/pages/Misc/SignupPage.vue';
import CheckoutPage from 'app/src/pages/Misc/CheckoutPage.vue';
import BrandGuidelines from 'app/src/pages/Brands/BrandGuidelines.vue';
import TaskOverview from 'app/src/pages/Brands/TaskOverview.vue';
import ProjectOverview from 'app/src/pages/Brands/ProjectOverview.vue';

// Handlers
import _Error_NotFound from 'src/pages/Misc/_Error_NotFound.vue';

const routes = [
  {
    path: '/',
    component: MainLayout,
    children: [
      { path: '', component: IndexPage },
      {
        path: 'brands',
        children: [
          { path: '', component: BrandsPage },
          {
            path: ':brandGID',
            children: [
              { path: '', component: BrandGuidelines },
              {
                path: 'task',
                children: [
                  {
                    path: ':taskId',
                    component: TaskOverview,
                  },
                ],
              },
              {
                path: 'project',
                children: [
                  {
                    path: ':projectId',
                    component: ProjectOverview,
                  },
                ],
              },
            ],
          },
        ],
      },
      { path: 'files', component: FilesPage },
      { path: 'help', component: HelpCenterPage },
      { path: 'discover', component: DiscoverPage },
      { path: 'gift', component: GiftPage },
      {
        path: 'settings',
        children: [
          { path: '', component: AccountSettingsPage },
          { path: ':settingsTab', component: AccountSettingsPage },
        ],
      },
    ],
  },
  {
    path: '/signup',
    component: SignupPage,
  },
  {
    path: '/checkout',
    component: CheckoutPage,
  },
  {
    path: '/auth',
    component: EmptyLayout,
    children: [{ path: '', component: AuthenticationPage }],
  },

  // Error Handlers
  {
    path: '/:catchAll(.*)*',
    component: _Error_NotFound,
  },
];

export default routes;
