<template>
  <q-card flat bordered class="rounded">
    <q-card-section
      :class="$q.screen.gt.sm ? 'q-px-lg q-py-md' : 'q-px-md q-py-sm'"
    >
      <q-toolbar class="text-primary q-pa-none">
        <q-toolbar-title class="text-h6 font-medium">
          Company Profile
        </q-toolbar-title>

        <div class="q-gutter-sm">
          <DynamicButton
            v-if="!editing"
            label="Edit Info"
            icon="icon-pencil-02"
            @click="editing = true"
          />

          <DynamicButton
            v-if="editing"
            outline
            :flat="$q.screen.lt.xs || $q.screen.xs"
            label="Cancel"
            icon="icon-x-close"
            @click="cancelEditing"
          />

          <DynamicButton
            v-if="editing"
            label="Save Changes"
            icon="icon-save-01"
            @click="saveOrgInfo"
          />
        </div>
      </q-toolbar>
    </q-card-section>
    <q-separator />
    <q-card-section
      class="q-col-gutter-md font-normal text-primary text-body2"
      :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'"
      :style="editing ? 'max-width: 900px' : ''"
    >
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Company Name<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            v-if="editing"
            dense
            outlined
            placeholder="Name"
            v-model="company.title"
          />
          <div v-else class="letter-space-normal">
            {{ selectedOrg.title }}
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Company Type<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <SelectCompanyType
            v-if="editing"
            v-model="company.type"
            :initModel="company.type"
          />
          <div v-else class="letter-space-normal">
            {{ companyTypes[selectedOrg.type]?.label }}
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium color-primary2"
        >
          About Us<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            v-if="editing"
            dense
            outlined
            type="textarea"
            placeholder="About the company..."
            v-model="company.about"
          />
          <div v-else class="letter-space-normal">
            {{ selectedOrg.about }}
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div
          class="col-12 col-sm-4 col-md-3 font-medium color-primary2 font-medium"
        >
          Website<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <InputWebsite v-if="editing" v-model="company.website" />
          <div v-else class="letter-space-normal">
            <external-link :url="selectedOrg.website" />
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium color-primary2 font-medium"
        >
          Address<span v-if="editing" class="text-red">*</span>
        </div>
        <div v-if="editing" class="col-12 col-sm-8 col-md-9">
          <div class="row q-col-gutter-sm items-center justify-start">
            <div class="col-12">
              <q-input
                dense
                outlined
                placeholder="Street Name"
                v-model="company.address_1"
              />
            </div>
            <div class="col-12">
              <q-input
                dense
                outlined
                placeholder="City"
                v-model="company.city"
              />
            </div>
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="State"
                v-model="company.state"
              />
            </div>
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="ZIP Code"
                v-model="company.zipcode"
              />
            </div>
            <div class="col-12">
              <SelectCountry
                v-model="company.country"
                :initModel="company.country"
              />
            </div>
          </div>
        </div>
        <div
          v-else
          class="col-12 col-sm-8 col-md-9 letter-space-normal q-gutter-sm"
        >
          <div>
            {{ selectedOrg.address_1 }}
          </div>
          <div>
            {{ selectedOrg.firstName }}
          </div>
          <div>
            {{ selectedOrg.city }}, {{ selectedOrg.state }},
            {{ selectedOrg.zipcode }}
          </div>
          <div>
            {{ selectedOrg.country }}
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Contact Number<span v-if="editing" class="text-red"></span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <InputPhoneNumber
            v-if="editing"
            @areaCodeChanged="(e) => (company.phone_code = e)"
            @numberChanged="(e) => (company.phone_number = e)"
            :areaCode="company.phone_code"
            :phoneNumber="company.phone_number"
          />
          <div v-else class="letter-space-normal">
            <external-link
              :url="`tel:+${selectedOrg.phone_code}${selectedOrg.phone_number}`"
              :label="`+${selectedOrg.phone_code} ${selectedOrg.phone_number}`"
            />
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium color-primary2 font-medium"
          v-if="editing || Object.values(selectedOrg.social || {})?.length"
        >
          Social Media Links
        </div>
        <div class="col-12 col-sm-8 col-md-9 letter-space-normal">
          <div v-if="editing" class="q-col-gutter-sm">
            <template v-for="(link, type) in company.social" :key="type">
              <q-input
                dense
                outlined
                placeholder="company_username"
                v-model="company.social[type]"
              >
                <template v-slot:prepend>
                  <div class="text-body2 color-primary2">
                    {{
                      getSocialInfo(type)
                        ?.baseurl.replace('http://', '')
                        .replace('https://', '')
                        .replace('www.', '')
                    }}{{ !type.endsWith('/') ? '/' : '' }}
                  </div>
                  <q-separator vertical class="q-ml-sm" />
                </template>
              </q-input>
            </template>
          </div>
          <div v-else class="letter-space-normal q-col-gutter-sm">
            <div v-for="(link, type) in selectedOrg.social" :key="type">
              <external-link :url="getSocialInfo(type)?.baseurl + '/' + link">
                <template v-slot:prepend>
                  <q-icon
                    :name="formatMediaPlatformIconSrc(type)"
                    size="xs"
                    class="self-end"
                  />
                </template>
              </external-link>
            </div>
          </div>
        </div>
      </div>
    </q-card-section>

    <!-- Dialog Unsaved Edit -->
    <q-dialog v-model="unsaved">
      <dynamic-dialog-confirmation
        v-bind="$root.messagesDialogs?.editingUnsaved"
        @okay="editing = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { objectCopy, url2link } from 'src/assets/scripts/functions';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import InputWebsite from 'src/components/Helpers/InputWebsite.vue';
import InputPhoneNumber from 'src/components/Helpers/InputPhoneNumber.vue';
import SelectCompanyType from 'src/components/Helpers/SelectCompanyType.vue';
import { mapActions, mapState } from 'pinia';
import useHelpers from 'stores/helpers';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import SelectCountry from '../Helpers/SelectCountry.vue';

export default defineComponent({
  name: 'CompanyProfile',

  components: {
    DynamicButton,
    InputPhoneNumber,
    SelectCompanyType,
    InputWebsite,
    SelectCountry,
  },

  data() {
    return {
      url2link: url2link,
      editing: false,
      unsaved: false,
      company: { colors: {}, social: {} },
    };
  },

  computed: {
    ...mapState(useHelpers, ['socialMedia']),
    ...mapState(useUser, ['selectedOrg']),
    ...mapState(useGrowmodo, ['companyTypes']),
  },

  watch: {
    editing: {
      async handler(val) {
        if (val) {
          // Editing
          // Update the editing value
          this.company = objectCopy(this.selectedOrg || {});
        } else {
          // Viewing
          // Reset company info
          this.company = {};
        }
      },
    },
  },

  methods: {
    ...mapActions(useUser, ['updateOrganization']),
    ...mapActions(useHelpers, ['getSocialInfo', 'formatMediaPlatformIconSrc']),

    saveOrgInfo() {
      this.updateOrganization(this.selectedOrg?.id, this.company);
      this.editing = false;
      this.$q.notify({
        icon: 'icon-check-circle-broken',
        message: 'Your changes have been successfully saved',
      });
    },

    cancelEditing() {
      this.unsaved = true;
    },
  },
});
</script>
