<script>
import { defineComponent } from 'vue';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import SelectCompanyType from 'src/components/Helpers/SelectCompanyType.vue';
import InputWebsite from 'src/components/Helpers/InputWebsite.vue';
import { mapActions, mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import SelectCountry from 'src/components/Helpers/SelectCountry.vue';
import SelectRoles from 'src/components/Helpers/SelectRoles.vue';
import SelectEmployeeCount from 'src/components/Helpers/SelectEmployeeCount.vue';
import GrowmodoTestimonials from 'src/components/GrowmodoTestimonials.vue';
import { updateStepperHeader } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'SignupPage',

  components: {
    GrowmodoTextIcon,
    SelectCompanyType,
    InputWebsite,
    InputPassword,
    SelectCountry,
    SelectRoles,
    SelectEmployeeCount,
    GrowmodoTestimonials,
  },

  data() {
    return {
      title: 'Signup',
      rightDrawer: false,
      account: {
        company: {},
      },
      formStep: 1,
      stepperHeaderElements: null,
      signupVerifyEmail: false,
    };
  },

  watch: {
    formStep: {
      async handler(val) {
        this.stepperHeaderStyle();
        if (val > 1) this.rightDrawer = false;
        else this.rightDrawer = true;
      },
    },
    signupVerifyEmail: {
      async handler(val) {
        setTimeout(() => {
          this.stepperHeaderElements =
            this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
          this.stepperHeaderStyle();
        }, 100);
      },
    },
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    this.stepperHeaderElements =
      this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
    this.stepperHeaderStyle();
  },

  computed: {
    ...mapState(useGrowmodo, ['companyTypes', 'subscriptionPlan_BillingTypes']),

    listCompanyTypes() {
      return Object.values(this.companyTypes);
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['planPriceCalculator']),

    stepperHeaderStyle() {
      if (this.stepperHeaderElements) {
        updateStepperHeader(this.stepperHeaderElements, this.formStep);
      } else {
        this.stepperHeaderElements =
          this.$refs?.['formStepper']?.$el?.firstChild?.childNodes;
      }
    },

    previousStep() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextStep() {
      this.$refs?.['formStepper']?.next?.();
    },

    gotoStep(step_no) {
      this.$refs?.['formStepper']?.goTo?.(step_no);
    },

    nextStepVerifyEmail() {
      this.signupVerifyEmail = true;
    },

    resendVerificationLink() {
      this.$q.notify({
        icon: 'icon-check-circle-broken',
        message: 'Verification link has been sent',
      });
    },

    submitSignup() {
      this.$q.notify({
        icon: 'icon-check-circle-broken',
        message: 'Navigating to external checkout page.',
      });
      this.$router.push('/');
    },
  },
});
</script>

