<template>
  <q-card flat class="rounded q-pb-md">
    <q-card-section :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'">
      <q-toolbar class="text-primary q-pa-none">
        <q-toolbar-title class="text-h6 font-medium">
          Users Information
        </q-toolbar-title>

        <div class="q-gutter-sm">
          <DynamicButton
            v-close-popup
            outline
            :flat="$q.screen.lt.xs || $q.screen.xs"
            label="Cancel"
            icon="icon-x-close"
          />
          <DynamicButton
            :label="newUser ? 'Invite User' : 'Save Changes'"
            icon="icon-save-01"
            @click="saveUserInfo"
          />
        </div>
      </q-toolbar>
    </q-card-section>

    <q-separator />

    <q-card-section
      :class="
        $q.screen.gt.sm ? 'q-pa-lg q-col-gutter-lg' : 'q-pa-md q-col-gutter-sm'
      "
      style="max-width: 800px"
    >
      <div class="row full-width items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Username<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            dense
            outlined
            v-model="account.username"
            placeholder="username"
          />
        </div>
      </div>
      <div class="row full-width items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Password<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <InputPassword v-model="account.password" />
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Full Name<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <div class="row q-col-gutter-sm items-center justify-start">
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="First Name"
                v-model="account.firstname"
              />
            </div>
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="Last Name"
                v-model="account.lastname"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          User Role<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-6 col-md-9">
          <SelectRoles v-model="account.role" :initModel="account.role" />
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Email Address<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            dense
            outlined
            placeholder="example@email.com"
            v-model="account.email"
          />
        </div>
      </div>
    </q-card-section>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import { checkIfObject, objectCopy } from 'src/assets/scripts/functions';
import { uid } from 'quasar';
import SelectRoles from 'src/components/Helpers/SelectRoles.vue';

export default defineComponent({
  name: 'Add_Edit_User',

  components: { DynamicButton, InputPassword, SelectRoles },

  emits: ['added', 'updated', 'error', 'close'],

  props: {
    uid: {
      type: [String, Number],
      default: null,
    },
  },

  data() {
    return {
      userInfo: null,
      account: {},
      newUser: true,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgUsers']),
  },

  mounted() {
    this.userInfo = this.selectedOrgUsers[this.uid];
    if (this.userInfo) {
      this.account = objectCopy(this.userInfo);
      this.newUser = false;
    }
  },

  methods: {
    ...mapActions(useUser, ['addOrgUser', 'updateOrgUser']),

    saveUserInfo() {
      if (
        !this.account ||
        !(checkIfObject(this.account) && Object.values(this.account).join(''))
      )
        return;
      if (this.newUser) {
        // Register New User
        let newUID = uid();
        this.account.password = undefined;
        this.account.id = newUID;
        this.addOrgUser(newUID, this.account);
        this.$emit('added', newUID);
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Your invitation has been sent',
        });
      } else {
        // Update Existing User
        this.updateOrgUser(this.uid, this.account);
        this.$emit('updated', this.uid);
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Your changes have been successfully saved',
        });
      }
    },
  },

  beforeUnmount() {
    this.$emit('close');
  },
});
</script>
