import { boot } from 'quasar/wrappers';
import { LoadingBar } from 'quasar';
import axios from 'axios';
import fnStore from 'src/stores/fn.store';
import config from 'src/assets/_config';

// Be careful when using SSR for cross-request state pollution
// due to creating a Singleton instance here;
// If any client changes this (global) instance, it might be a
// good idea to move this instance creation inside of the
// "export default () => {}" function below (which runs individually
// for each client)

const api = axios.create({ baseURL: config?.api?.baseURL });

export default boot(({ app, store, router }) => {
  const apiRequestHandler = (request) => {
    if (fnStore.getAuth())
      request.headers.Authorization = `Bearer ${fnStore.getAuth()}`;
    LoadingBar.start();
    return request;
  };

  const apiResponseHandler = (response) => {
    if (response.status !== 200)
      if (process.env.DEBUGGING)
        console.warn('API Status:', response.status, response);
    LoadingBar.stop();
    return response;
  };

  const apiErrorHandler = (error) => {
    if (process.env.DEBUGGING) console.error('API Error:', error);
    LoadingBar.stop();
    return Promise.reject(error);
  };

  // Intercept all api request
  api.interceptors.request.use(
    (request) => apiRequestHandler(request),
    (error) => apiErrorHandler(error)
  );

  // Intercept all api response
  api.interceptors.response.use(
    (response) => apiResponseHandler(response),
    (error) => apiErrorHandler(error)
  );

  // This function will run everytime router path changes
  router.beforeEach((to, from, next) => {
    if (fnStore.getAuth() && to.path == '/auth') {
      next(to.query?.redirect || '/');
    } else if (
      !fnStore.getAuth() &&
      to.path != '/auth' &&
      !to.query?.redirect &&
      !config.publicPath.includes(to.fullPath)
    ) {
      // store.commit('user/unsetAuthenticated');
      const pathUri = encodeURIComponent(to.fullPath);
      next(`/auth?redirect=${pathUri}`);
    } else {
      next();
    }
  });

  // for use inside Vue files (Options API) through this.$axios and this.$api
  app.config.globalProperties.$axios = axios;
  // ^ ^ ^ this will allow you to use this.$axios (for Vue Options API form)
  //       so you won't necessarily have to import axios in each vue file

  app.config.globalProperties.$api = api;
  // ^ ^ ^ this will allow you to use this.$api (for Vue Options API form)
  //       so you can easily perform requests against your app's API
});

export { axios, api };
