                class="cursor-pointer rounded hover-text-secondary hover-bg-accent text-body2 font-medium"
                :class="
                  (tab.key === tabSectionId && tabSectionIdScrolled === '') ||
                  tabSectionIdScrolled === tab.key ||
                  (tabSectionIdScrolled === '' &&
                    tabSectionId === '' &&
                    i === 0)
                    ? 'bg-accent text-secondary'
                    : 'color-primary2'
                "
                @click="clickScrollEvent(tab.key)"
              >
                <q-btn
                  flat
                  no-caps
                  :ripple="false"
