<template>
  <q-layout view="LHh lpR lFf">
    <DrawerMain />

    <q-page-container class="bg-color">
      <router-view v-slot="{ Component }">
        <q-pull-to-refresh
          @refresh="refresh"
          color="secondary"
          icon="icon-refresh-cw-01"
          bg-color="translucent-light shadow-ui"
        >
          <component :is="Component" />
        </q-pull-to-refresh>

        <!-- Dev Tools -->
        <q-page-sticky
          style="z-index: 10"
          :offset="$q.screen.lt.sm ? [10, 0] : [10, 25]"
          :position="$q.screen.lt.sm ? 'right' : 'bottom-right'"
        >
          <q-fab
            flat
            unelevated
            direction="up"
            v-model="devTools"
            padding="sm"
            color="primary"
            icon="icon-growmodo"
            vertical-actions-align="right"
            v-ripple="{ color: 'accent' }"
            class="hover-bg-accent"
          >
            <q-fab-action
              label-position="left"
              color="primary"
              @click="$router.push('/signup')"
              label="Signup"
              class="rounded"
            />
            <q-fab-action
              v-if="isDebugging"
              label-position="left"
              color="primary"
              @click="sendTestNotif"
              label="Test Notif"
              class="rounded"
            />
            <q-fab-action
              label-position="left"
              color="primary"
              @click="$router.push('/404')"
              label="404 Page"
              class="rounded"
            />
            <q-fab-action
              label-position="left"
              color="primary"
              @click="startOffBoarding = true"
              label="Offboarding"
              class="rounded"
            />
            <q-fab-action
              label-position="left"
              color="primary"
              @click="userNotOnBoard = true"
              label="Onboarding"
              class="rounded"
            />
          </q-fab>
          <tooltip-pro
            v-if="!devTools"
            anchor="center left"
            self="center right"
          >
            Test Views
          </tooltip-pro>
        </q-page-sticky>

        <q-page-sticky
          position="bottom-right"
          v-if="$q.screen.width <= 600"
          :offset="[18, 18]"
          style="z-index: 9"
        >
          <q-btn
            fab
            flat
            icon="icon-grid-01"
            :ripple="{ color: 'accent' }"
            class="bg-primary text-white hover-bg-secondary"
            @click="$root.toggleMainDrawer()"
          />
          <tooltip-pro
            :delay="150"
            :offset="[10, 10]"
            self="center right"
            anchor="center left"
          >
            {{ $root.mainDrawer ? 'Hide' : 'Show' }} Menu
          </tooltip-pro>
        </q-page-sticky>

        <q-page-sticky
          position="bottom-right"
          :offset="[5, 2]"
          style="z-index: 9"
        >
          <div class="text-caption text-grey-9 no-pointer-events">
            {{ $config.pkjson.productName }} v{{ $config.pkjson.version }}
          </div>
        </q-page-sticky>
      </router-view>
    </q-page-container>

    <!-- Onboarding -->
    <q-dialog
      :persistent="startOnBoarding"
      :maximized="startOnBoarding"
      v-model="userNotOnBoard"
    >
      <OnBoarding
        noclose
        @start="initializeOnBoarding"
        class="rounded"
        :style="startOnBoarding ? '' : 'width: 980px; max-width: 80vw'"
        @exit="
          () => {
            userNotOnBoard = false;
            startOnBoarding = false;
          }
        "
      />
    </q-dialog>

    <!-- Offboarding -->
    <q-dialog persistent v-model="startOffBoarding">
      <OffBoarding
        class="rounded"
        style="width: 1080px; max-width: 80vw"
        @exit="
          () => {
            startOffBoarding = false;
          }
        "
      />
    </q-dialog>

    <!-- Dialog Request to Upgrade Plan -->
    <q-dialog v-model="$root.dialogRequestUpgrade">
      <dynamic-dialog-confirmation
        width="375px"
        v-bind="$root.messagesDialogs?.requestUpgrade"
      />
    </q-dialog>

    <!-- Request Option (Task/Project) -->
    <q-dialog
      full-width
      full-height
      :maximized="false"
      v-model="$root.request.new"
      transition-show="fade"
      transition-hide="fade"
      :transition-duration="300"
    >
      <RequestOptions class="rounded" :noclose="false" />
    </q-dialog>

    <!-- Task Directory -->
    <q-dialog
      full-width
      full-height
      v-model="$root.request.task"
      transition-show="fade"
      transition-hide="fade"
      :transition-duration="300"
    >
      <DirectoryLayout
        class="rounded"
        title="Task Directory"
        directoryType="task"
      />
    </q-dialog>

    <!-- Project Directory -->
    <q-dialog
      full-width
      full-height
      v-model="$root.request.project"
      transition-show="fade"
      transition-hide="fade"
      :transition-duration="300"
    >
      <DirectoryLayout
        class="rounded"
        title="Project Directory"
        directoryType="project"
      />
    </q-dialog>

    <!-- Create a Task -->
    <q-dialog
      maximized
      persistent
      full-width
      full-height
      v-model="$root.task.new"
      transition-show="fade"
      transition-hide="fade"
      :transition-duration="300"
    >
      <FormTaskCreate noclose />
    </q-dialog>

    <!-- Create a Project -->
    <q-dialog
      maximized
      persistent
      full-width
      full-height
      v-model="$root.project.new"
      transition-show="fade"
      transition-hide="fade"
      :transition-duration="300"
    >
      <FormProjectCreate noclose />
    </q-dialog>

    <!-- Plan Upgrade -->
    <q-dialog
      v-model="$root.upgradePlan"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <Dialog_UpgradeYourPlan
        noclose
        style="width: 640px"
        :style="
          $q.screen.xs && $q.screen.lt.sm
            ? 'max-width: 95vw;'
            : 'max-width: 80vw'
        "
        @nextBtnClick="$root.upgradePlan = false"
      />
    </q-dialog>

    <!-- Redirect Confirmation -->
    <q-dialog
      v-model="$root.redirect.confirmation"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <RedirectConfirmation
        v-bind="{ options: $root.redirect }"
        style="width: 350px"
      />
    </q-dialog>
  </q-layout>
</template>

<script>
import { defineComponent } from 'vue';
import { LoadingBar } from 'quasar';
import DrawerMain from 'src/components/Drawer_Main.vue';
import RequestOptions from 'src/components/Requests/Request_Options.vue';
import DirectoryLayout from 'src/components/Requests/DirectoryLayout.vue';
import FormTaskCreate from 'src/components/Forms/Form_Task_Create.vue';
import FormProjectCreate from 'src/components/Forms/Form_Project_Create.vue';
import RedirectConfirmation from 'src/components/Dialogs/RedirectConfirmation.vue';
import Dialog_UpgradeYourPlan from 'src/components/Dialogs/UpgradeYourPlan.vue';
import { displayNotification } from 'src/assets/scripts/notification';
import OnBoarding from 'src/components/Dialogs/OnBoarding.vue';
import OffBoarding from '../components/Dialogs/OffBoarding.vue';
import { mapActions } from 'pinia';
import useUser from 'stores/user';
import fnStore from 'src/stores/fn.store';

export default defineComponent({
  name: 'MainLayout',

  components: {
    DrawerMain,
    RequestOptions,
    FormTaskCreate,
    FormProjectCreate,
    RedirectConfirmation,
    Dialog_UpgradeYourPlan,
    DirectoryLayout,
    OnBoarding,
    OffBoarding,
  },

  data() {
    return {
      devTools: false,
      isDebugging: process.env.DEBUGGING,
      userNotOnBoard: false,
      startOnBoarding: false,
      startOffBoarding: false,
    };
  },

  computed: {
    token() {
      return fnStore.getAuth();
    },
  },

  watch: {
    token: {
      async handler(val) {
        if (process.env.DEBUGGING)
          console.info('Token Changed:', this.token, val);
      },
    },
  },

  methods: {
    ...mapActions(useUser, ['updateAccount']),

    onBoarded() {
      this.updateAccount({ onBoard: true });
    },

    initializeOnBoarding() {
      // this.userNotOnBoard = false;
      this.startOnBoarding = true;
      // setTimeout(() => {
      //   this.onBoarded();
      // }, 1500);
    },

    refresh() {
      setTimeout(() => {
        window.location.href = window.location.href;
      }, 1000);
    },

    sendTestNotif() {
      displayNotification('Test Notification', {
        body: 'Notif click event will refresh the page.',
      }).then((notif) => {
        notif.onclick = () => {
          window.location.href = window.location.href;
        };
      });
    },
  },

  async created() {
    this.$router.beforeEach((to, from, next) => {
      next();
      LoadingBar.start();
    });

    this.$router.afterEach((to, from, failure) => {
      setTimeout(() => {
        LoadingBar.stop();
      }, 500);
    });

    // Check if user is onboarded
    // setTimeout(() => {
    //   this.userNotOnBoard = this.user?.onBoard === false;
    // }, 1500);
  },
});
</script>
