<template>
  <div class="q-gutter-y-lg">
    <q-card flat bordered class="rounded">
      <q-card-section
        :class="$q.screen.gt.sm ? 'q-px-lg q-py-md' : 'q-px-md q-py-sm'"
      >
        <q-toolbar class="text-primary q-pa-none">
          <q-toolbar-title class="text-h6 font-medium">
            Your Current Plan
          </q-toolbar-title>

          <div class="q-gutter-sm">
            <DynamicButton
              v-if="!editing"
              label="Edit Subscription"
              icon="icon-pencil-02"
              @click="editing = true"
            />

            <DynamicButton
              v-if="editing"
              outline
              :flat="$q.screen.lt.xs || $q.screen.xs"
              label="Cancel Changes"
              icon="icon-x-close"
              @click="editing = false"
            />
          </div>
        </q-toolbar>
      </q-card-section>
      <q-separator />
      <q-card-section :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'">
        <div class="row items-center q-col-gutter-md q-py-sm">
          <div class="col-12 col-md-4 text-secondary text-h6">
            {{ subscriptionPlan.label }}
            {{ selectedOrg?.subscriptionPlan?.subscription_talent_count }}
          </div>
          <div class="col-12 col-md-8">
            <div class="q-pb-sm" style="max-width: 325px">
              <RemainingDaysProgress />
            </div>
            <div class="text-primary text-body2">
              {{ subscriptionPlan.description_1 }}
              {{ talentCount === -1 ? 'custom' : toWords(talentCount) }}
              {{ talentCount > 1 ? 'users' : 'user' }}.
            </div>
          </div>
        </div>
      </q-card-section>
    </q-card>

    <q-card v-if="editing" flat bordered class="rounded">
      <q-card-section :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'">
        <SubscriptionPlans
          :nextButton="true"
          @updated="planUpdated"
          @nextBtnClick="nextBtnClick"
        />
      </q-card-section>
    </q-card>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import SubscriptionPlans from 'src/components/Helpers/Subscription/SubscriptionPlans.vue';
import { mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import RemainingDaysProgress from 'src/components/Helpers/Subscription/RemainingDays-Progress.vue';
import { toWords } from 'number-to-words';

export default defineComponent({
  name: 'SubscriptionInformation',

  components: { DynamicButton, SubscriptionPlans, RemainingDaysProgress },

  setup() {
    const user = useUser();
    return { user };
  },

  data() {
    return {
      editing: false,
      toWords,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrg']),
    ...mapState(useGrowmodo, ['subscriptionPlan']),

    talentCount() {
      return this.selectedOrg?.subscriptionPlan?.subscription_talent_count;
    },
  },

  methods: {
    nextBtnClick() {
      this.editing = false;
      this.$root.dialogRequestUpgrade = true;
    },

    planUpdated(planinfo) {
      if (process.env.DEBUGGING) console.log('Plan Changed:', planinfo);
    },
  },
});
</script>
