import { register } from 'register-service-worker';
import { Notify, Dialog } from 'quasar';
import { displayNotification } from 'src/assets/scripts/notification';

// The ready(), registered(), cached(), updatefound() and updated()
// events passes a ServiceWorkerRegistration instance in their arguments.
// ServiceWorkerRegistration: https://developer.mozilla.org/en-US/docs/Web/API/ServiceWorkerRegistration

register(process.env.SERVICE_WORKER_FILE, {
  // The registrationOptions object will be passed as the second argument
  // to ServiceWorkerContainer.register()
  // https://developer.mozilla.org/en-US/docs/Web/API/ServiceWorkerContainer/register#Parameter

  registrationOptions: {
    scope: './',
    updateViaCache: 'all',
    type: 'classic',
  },

  async ready(registration) {
    let message = 'Service worker is active.';
    // Notify.create({
    //   icon: 'icon-check-circle-broken',
    //   message,
    // });
    if ('periodicSync' in registration) {
      try {
        await registration.periodicSync.register('growmodo-hub-content-sync', {
          minInterval: 1000 * 60 * 60, // hourly sync
        });
      } catch (error) {
        if (process.env.DEBUGGING)
          console.error('Periodic background sync cannot be used');
      }
    }
    if (process.env.DEBUGGING) console.info(message);
  },

  async registered(registration) {
    let message = 'Service worker has been registered.';
    if (process.env.DEBUGGING) console.info(message);
    // Notify.create({
    //   icon: "icon-download-04",
    //   message,
    // });

    setInterval(() => {
      if (process.env.DEBUGGING) console.info('Checking for update.');
      registration.update();
    }, 1000 * 60 * 60); // hourly checks
  },

  async cached(registration) {
    let message = 'Content has been cached for offline use.';
    // Notify.create({
    //   icon: "icon-file-download-01",
    //   message,
    // });
    if (process.env.DEBUGGING) console.info(message);
  },

  async updatefound(registration) {
    let message = 'New content is downloading.';
    // Notify.create({
    //   icon: 'icon-download-04',
    //   message,
    // });
    if (process.env.DEBUGGING) console.info(message);
  },

  async updated(registration) {
    let title = 'App has been Updated!';
    let message = 'New content is available; please refresh.';
    const { productName } = require('../package.json');
    displayNotification(productName + ' - ' + title, { body: message }).then(
      (notif) => {
        notif.onclick = () => {
          window.location.href = window.location.href;
        };
      }
    );

    Dialog.create({
      ok: {
        label: 'Refresh',
        rounded: true,
        noCaps: true,
        unelevated: true,
        size: 'md',
        color: 'primary',
        icon: 'icon-refresh-cw-01',
        class: 'font-normal rounded',
      },
      cancel: {
        label: '',
        flat: true,
        round: true,
        noCaps: true,
        unelevated: true,
        size: 'md',
        color: 'primary',
        icon: 'icon-x-close',
        class: 'absolute-top-right q-ma-sm',
      },
      seamless: true,
      noRouteDismiss: true,
      transitionHide: 'slide-down',
      transitionShow: 'slide-up',
      title,
      message,
      position: 'bottom',
      class: 'rounded q-mb-xl border-add q-pa-xs shadow-ui',
    }).onOk(() => {
      window.location.href = window.location.href;
    });
    if (process.env.DEBUGGING) console.info(message);
  },

  async offline() {
    let message = 'Using offline mode. Some features are not available!';
    // Notify.create({
    //   icon: 'icon-wifi-off',
    //   message,
    // });

    Dialog.create({
      ok: false,
      cancel: {
        label: '',
        flat: true,
        round: true,
        noCaps: true,
        unelevated: true,
        size: 'md',
        color: 'primary',
        icon: 'icon-minus',
        class: 'absolute-top-right q-ma-sm hide',
      },
      seamless: true,
      noRouteDismiss: true,
      transitionHide: 'slide-up',
      transitionShow: 'slide-down',
      position: 'top',
      title: 'Offline Mode!',
      message,
      class: 'rounded border-add shadow-ui q-ma-md action-absolute',
    });
    if (process.env.DEBUGGING) console.info(message);
  },

  async error(err) {
    let message = 'Error during service worker registration.';
    // Notify.create({
    //   icon: 'icon-alert-circle',
    //   message,
    // });
    if (process.env.DEBUGGING) console.error(message + ':', err);
  },
});
