q {\n  &-btn {\n    &:not(.q-btn--flat, .not-gm, .disabled) {\n      &:before {\n        transition: $shadow-transition;\n      }\n      &:hover {\n        &:before {\n          box-shadow: $shadow-gm;\n        }\n        & .q-focus-helper {\n          opacity: 0 !important;\n        }\n      }\n    }\n  }\n\n  &-field {\n    &.format-bold {\n      &.q-field {\n        &-focused,\n        &--highlighted,\n        &:hover,\n        &:active,\n        &:focus-visible {\n          & .q-field {\n            &__control {\n              &:before,\n              &:after {\n                border-color: $secondary !important;\n              }\n            }\n          }\n          & .q-separator {\n            border-color: $secondary !important;\n          }\n        }\n        & .q-separator {\n          width: 0;\n          border-left: map-get($borders, 'md') solid $primary;\n        }\n        &--outlined {\n          & .q-field {\n            &__control {\n              &:before,\n              &:after {\n                border-width: map-get($borders, 'md');\n                border-color: $primary;\n              }\n            }\n          }\n        }\n      }\n      &-light {\n        &.q-field {\n          &--outlined {\n            & .q-field {\n              &__control {\n                &:before,\n                &:after {\n                  border-width: map-get($borders, 'md');\n                  border-color: $border-color;\n                }\n              }\n            }\n          }\n        }\n      }\n    }\n    &:not(.format-bold) {\n      &.q-field {\n        &-focused,\n        &--highlighted,\n        &:hover,\n        &:active,\n        &:focus-visible {\n          & .q-field {\n            &__control {\n              &:before,\n              &:after {\n                border-width: map-get($borders, 'normal');\n              }\n            }\n          }\n          & .hover {\n            &-bg {\n              &-primary {\n                background: $primary;\n              }\n              &-secondary {\n                background: $secondary;\n              }\n              &-accent {\n                background: $accent;\n              }\n            }\n            &-text {\n              &-primary {\n                color: $primary;\n              }\n              &-secondary {\n                color: $secondary;\n              }\n              &-accent {\n                color: $accent;\n              }\n            }\n          }\n          & .q-separator {\n            border-color: $secondary !important;\n          }\n        }\n        & .q-separator {\n          width: 0;\n          border-left: map-get($borders, 'normal') solid $border-color;\n        }\n      }\n      &.q-field--outlined {\n        & .q-field {\n          &__control {\n            &:before,\n            &:after {\n              border-width: map-get($borders, 'normal');\n            }\n          }\n        }\n      }\n    }\n    &.table-select-filter {\n      & .q-field__native {\n        font-weight: $font-medium;\n        font-size: map-get($body2, 'size');\n      }\n    }\n    &.q-field {\n      &--focused,\n      &--highlighted,\n      &:hover {\n        & .q-field {\n          &__control {\n            &:not(.text-negative) {\n              &:before,\n              &:after {\n                border-color: $secondary;\n              }\n            }\n          }\n        }\n      }\n    }\n    &--outlined {\n      &:before {\n        border-width: map-get($borders, 'normal');\n      }\n      & .q-field {\n        &__control {\n          border-radius: $border;\n          &:before,\n          &:after {\n            border-color: $border-color;\n          }\n        }\n      }\n    }\n    &--dense {\n      &:not(.q-select, .q-textarea) {\n        & .q-field {\n          &__control,\n          &__marginal {\n            height: 45px;\n          }\n        }\n      }\n      & .q-field {\n        &__control,\n        &__marginal {\n          min-height: 45px;\n        }\n        &__bottom {\n          font-size: map-get($body2, 'size') !important;\n          padding: 8px 0;\n        }\n      }\n    }\n    &.q-select {\n      & .q-select {\n        &__dropdown-icon {\n          color: $placeholder-color;\n        }\n      }\n    }\n  }\n\n  &-textarea {\n    & .q-field__control {\n      height: auto;\n    }\n  }\n\n  &-dialog {\n    &__title {\n      letter-spacing: map-get($letter-spacing, 'standard');\n    }\n    &:not(.q-dialog--seamless) {\n      // Default dark background\n      background-color: rgba(0, 0, 0, 0.8);\n    }\n    &.backdrop {\n      &-light {\n        & .q-dialog__backdrop {\n          background-color: rgba(0, 0, 0, 0.4);\n        }\n      }\n      &-blur {\n        & .q-dialog__backdrop {\n          backdrop-filter: blur(7px);\n        }\n      }\n    }\n  }\n\n  &-card {\n    &.action {\n      &-absolute {\n        & .q-card__actions {\n          margin: 0;\n          padding: 0;\n          height: 0;\n          min-height: unset;\n        }\n      }\n      &-none {\n        & .q-card__actions {\n          display: none;\n        }\n      }\n    }\n    &--bordered {\n      border-color: $border-color;\n    }\n  }\n\n  &-uploader {\n    &.files-uploader {\n      & .q-uploader {\n        &__header {\n          height: inherit;\n          background: transparent;\n        }\n        &__list {\n          padding: 0;\n          min-height: unset;\n        }\n      }\n      &.with-items {\n        max-height: 450px;\n      }\n      &:not(.with-items) {\n        & .q-uploader {\n          &__list {\n            display: none;\n          }\n        }\n      }\n    }\n  }\n\n  &-pagination {\n    &.rounded {\n      & .q-btn-item {\n        border-radius: $border;\n      }\n    }\n  }\n\n  &-rating {\n    &.no-shadow {\n      & .q-rating__icon {\n        text-shadow: unset;\n      }\n    }\n  }\n\n  &-stepper {\n    &.title-only {\n      & .q-stepper {\n        &__title {\n          font-size: map-get($body2, 'size');\n        }\n        &__step {\n          &-content {\n            display: none;\n          }\n        }\n        &__dot {\n          background-color: transparent;\n          &:before,\n          &:after {\n            background-color: white;\n          }\n          & .q-icon {\n            font-size: 26px;\n          }\n        }\n      }\n      &.rounded {\n        & .q-stepper {\n          &__tab {\n            border-radius: $border;\n          }\n        }\n      }\n    }\n    &__step {\n      &-inner {\n        padding-top: 0 !important;\n        padding-bottom: 0 !important;\n      }\n      &.full {\n        &-width {\n          & .q-stepper {\n            &__step {\n              &-content {\n                padding: 0;\n                width: 100% !important;\n                margin-left: 0 !important;\n                margin-right: 0 !important;\n              }\n            }\n          }\n        }\n        &-inner {\n          & .q-stepper {\n            &__step {\n              &-inner {\n                padding: 12px;\n                width: 100% !important;\n                margin-left: 0 !important;\n                margin-right: 0 !important;\n              }\n            }\n          }\n        }\n      }\n    }\n    & .tab-no-padding {\n      & .q-stepper {\n        &__tab {\n          padding-left: 0;\n          padding-right: 0;\n        }\n      }\n    }\n  }\n\n  &-radio {\n    &.flex-label {\n      & .q-radio {\n        &__label {\n          display: flex;\n          align-items: center;\n        }\n      }\n    }\n  }\n\n  &-table {\n    & .q-table {\n      &__progress > th {\n        position: sticky;\n        z-index: 4;\n        top: 1px;\n      }\n    }\n    &__container {\n      & tbody {\n        & tr {\n          &.selected {\n            & td {\n              &:before,\n              &:after {\n                background: unset !important;\n              }\n            }\n          }\n          & td {\n            &:before,\n            &:after {\n              background: unset !important;\n            }\n          }\n        }\n      }\n    }\n  }\n\n  &-stepper {\n    &.small {\n      & .q-stepper {\n        &__title {\n          font-size: map-get($subtitle1, 'size');\n        }\n        &__dot {\n          width: 26px;\n          min-width: 26px;\n          height: 26px;\n          & span {\n            font-size: map-get($body1, 'size');\n          }\n        }\n      }\n    }\n    &:not(.small) {\n      & .q-stepper {\n        &__title {\n          font-size: map-get($subtitle1, 'size');\n        }\n        &__dot {\n          width: 40px;\n          min-width: 40px;\n          height: 40px;\n          & span {\n            font-size: map-get($subtitle1, 'size');\n          }\n        }\n      }\n    }\n    &__title {\n      font-weight: $font-normal;\n      letter-spacing: map-get($letter-spacing, 'normal');\n    }\n    &__tab {\n      &--active {\n        & .q-stepper {\n          &__title {\n            font-weight: $font-medium;\n          }\n        }\n      }\n    }\n    &.new-brand-stepper {\n      & .q-stepper {\n        &__tab {\n          &:first-child,\n          &:last-child {\n            padding-left: 0 !important;\n            padding-right: 0 !important;\n          }\n        }\n      }\n    }\n  }\n  &-date {\n    &__calendar {\n      &-item {\n        & .q-btn {\n          &:before {\n            box-shadow: unset !important;\n          }\n        }\n      }\n    }\n  }\n}\n","// Quasar SCSS (& Sass) Variables\n// --------------------------------------------------\n// To customize the look and feel of this app, you can override\n// the Sass/SCSS variables found in Quasar's source Sass/SCSS files.\n\n// Check documentation for full list of Quasar variables\n\n// Your own variables (that are declared here) and Quasar's own\n// ones will be available out of the box in your .vue/.scss/.sass files\n\n// It's highly recommended to change the default colors\n// to match your app's branding.\n// Tip: Use the \"Theme Builder\" on Quasar's documentation website.\n// https://quasar.dev/style/sass-scss-variables\n\n// Themes\n$primary: #121212;\n$primary2: #5b6279;\n$secondary: #2b67ff;\n$accent: #f6f9ff;\n$accent2: #fcfcfd;\n\n$dark: #0a122a;\n$dark-page: #0a122a;\n\n$gm-logo: (\n  color-1: #3867f6,\n  color-2: #101010,\n  color-3: #d2fd51,\n);\n\n$positive: #1976d2;\n$negative: #d92d20;\n$info: #00a761;\n$warning: #fec84b;\n\n/// Replace `$search` with `$replace` in `$string`\n/// @author Hugo Giraudel\n/// @param {String} $string - Initial string\n/// @param {String} $search - Substring to replace\n/// @param {String} $replace ('') - New value\n/// @return {String} - Updated string\n@function str-replace($string, $search, $replace: '') {\n  $index: str-index($string, $search);\n\n  @if $index {\n    @return str-slice($string, 1, $index - 1) + $replace +\n      str-replace(\n        str-slice($string, $index + str-length($search)),\n        $search,\n        $replace\n      );\n  }\n\n  @return $string;\n}\n\n// Custom Variables\n$placeholder-color: rgba($primary, 0.5);\n$border-color: #e0e0e0;\n\n$translucent: 0.3;\n$transition-durations: (\n  normal: 0.3s,\n  md: 0.5s,\n  lg: 0.7s,\n  xl: 1s,\n);\n\n$generic-hover-transition: map-get($transition-durations, 'normal')\n  cubic-bezier(0.25, 0.8, 0.5, 1);\n\n$letter-spacing: (\n  standard: -0.5px,\n  normal: 0px,\n  sm: 0.1rem,\n  md: 0.2rem,\n  lg: 0.3rem,\n);\n\n// Growmodo Extra Colors\n$gmc-green: #bcf200;\n\n$border: 8px;\n$borders: (\n  normal: 1px,\n  md: 1.5px,\n  lg: 2px,\n  xl: 3px,\n);\n\n$active-bg: #ecfdf3;\n$active-color: #027a48;\n$negative-bg: #fef3f2;\n$negative-color: #b42318;\n$disable-bg: #f8f9fc;\n$disable-color: #363f72;\n$blue-bg: #eff8ff;\n$blue-color: $secondary;\n$warning-bg: #fffaeb;\n$warning-color: #b54708;\n\n$font-extra-light: 100;\n$font-thin: 150;\n$font-light: 200;\n$font-normal: 400;\n$font-medium: 500;\n$font-de