function getPlatformInfo(platform, key = null) {
  return platform
    ? this.platforms[platform] && key
      ? this.platforms?.[platform]?.[key]
      : this.platforms?.[platform] || platform
    : 0;
}

function formatPlatformIconSrc(platform) {
  return (
    'img:icons/platform/' +
    (this.getPlatformInfo(platform)?.icon || platform) +
    '.png'
  );
}

function planPriceCalculator(talentCnt, billingId) {
  if (!talentCnt || !billingId) return {};
  const { formatMoney } = require('src/assets/scripts/functions');
  const billing = this.subscriptionPlan_BillingTypes[billingId] || {};

  // Total Price
  let total =
    talentCnt === -1 || talentCnt === 6
      ? 0
      : this.subscriptionPlan.base_price * talentCnt * billing.value;
  // Discount
  let discount =
    (billing.savings_percentage ? total * billing.savings : billing.savings) ||
    0;
  // Discount Rounded by 50
  let discount_rounded = Math.round(discount / 50) * 50;
  // Total Discount check if savings_per_month
  let discount_total =
    discount_rounded * (billing.savings_per_month ? billing.value : 1);
  // Total Discounte Price
  let total_discounted = total - discount_total;
  // Discount Label
  let discount_label =
    total === 0
      ? 'Custom'
      : billing.savings_currency_legend + formatMoney(discount_total);
  // Final Label with Total Discounted Price
  let label =
    total === 0
      ? 'Custom'
      : this.subscriptionPlan.price_currency_legend +
        formatMoney(total_discounted);

  return {
    label,
    total,
    total_discounted,
    discount,
    discount_rounded,
    discount_label,
    discount_total,
  };
}

export { getPlatformInfo, formatPlatformIconSrc, planPriceCalculator };
